.cards-slider-wrapper {
    display: -webkit-flex;
    display: flex;
    position: absolute;
    -webkit-transition: -webkit-transform 300ms ease-in;
    transition: -webkit-transform 300ms ease-in;
    transition: transform 300ms ease-in;
    transition: transform 300ms ease-in, -webkit-transform 300ms ease-in;
}

.card {
    -webkit-flex: 1 1;
            flex: 1 1;
    opacity: 0.5;
    -webkit-transform: scale(0.6);
            transform: scale(0.6);
    -webkit-transition: opacity 300ms linear;
    transition: opacity 300ms linear;     -webkit-transition: -webkit-transform 300ms ease-in;     transition: -webkit-transform 300ms ease-in;     transition: transform 300ms ease-in;     transition: transform 300ms ease-in, -webkit-transform 300ms ease-in;
}


